<template>
	<div class="modal fade" id="contactModal" tabindex="-1" role="dialog" aria-labelledby="contactModalTitle" aria-hidden="true">
		<div class="modal-dialog modal-lg" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="contactModalTitle">Add Course</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form
						@submit.prevent="createClass"
						class="modal__form"
						method="post">
						<div class="form-group">
							<label class="modal__label" for="class-title">Title</label>
							<input
								type="text"
								:class="[{'is-invalid': errors.title}]"
								class="form-control"
								id="class-title"
								placeholder="Class Title"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.title">
							<div class="invalid-feedback">
								{{ errors.title }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="career">Career Track</label>
							<select
								class="custom-select form-control"
								id="career"
								:class="[{'is-invalid': errors.career}]"
								v-model="itemData.career_id">
								<option selected disabled>Select a career for track for this course</option>
								<option
									v-for="(career, i) in careers"
									:key="i"
									:value="career.id">{{career.name}}</option>
							</select>
							<div class="invalid-feedback">
								{{ errors.career_id }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="class-exam-attempts">Exam Attempts</label>
							<input
								type="number"
								:class="[{'is-invalid': errors.exam_attempts}]"
								class="form-control"
								id="class-title"
								placeholder="Total Number of time a candidate can take the exams attached to this course"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.exam_attempts">
							<div class="invalid-feedback">
								{{ errors.exam_attempts }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="class-exam-attempts">Total Exam Question</label>
							<input
								type="number"
								:class="[{'is-invalid': errors.total_exam_question}]"
								class="form-control"
								id="class-title"
								placeholder="Total exam questions that should be made available to candidates."
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.total_exam_question">
							<div class="invalid-feedback">
								{{ errors.total_exam_question }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="class-exam-attempts">Exam Pass Grade</label>
							<input
								type="number"
								:class="[{'is-invalid': errors.pass_grade}]"
								class="form-control"
								id="class-title"
								placeholder="The pass grade for an exam."
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.pass_grade">
							<div class="invalid-feedback">
								{{ errors.pass_grade }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="class-title">Exam Duration</label>
							<input
								type="number"
								:class="[{'is-invalid': errors.exam_duration}]"
								class="form-control"
								id="exam-duration"
								placeholder="Enter class exam duration"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.exam_duration">
							<div class="invalid-feedback">
								{{ errors.exam_duration }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="class-desc">Description</label>
							<textarea
								:class="[{'is-invalid': errors.description}]"
								class="form-control"
								placeholder="Enter class description"
								id="description"
								cols="30"
								rows="10"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.description"></textarea>
							<div class="invalid-feedback">
								{{ errors.description }}
							</div>
						</div>
						<div class="form-group exam-instructions">
							<label class="modal__label" for="class-desc">Exam Instructions</label>
							<editor :init="init" ref="myTextEditor"
								v-model="itemData.exam_instructions"
								:options="editorOption">
							</editor>
							<div class="invalid-feedback">
								{{ errors.exam_instructions }}
							</div>
						</div>
						<div class="form-group course-image-upload">
							<label class="modal__label" for="class-image-file">
								Add Course Image
							</label>
							<input
								ref="avatar"
								class="avatar__input"
								id="class-image-file"
								type="file"
								@change="loadImage"
								accept="image/*">
							<div class="invalid-feedback">
								{{ errors.file }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button
						@click="clearErrors()"
						class="btn btn--md"
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button
						@click.prevent="createClass"
						class="btn btn--md btn__primary"
						type="submit"
						name="button">
						Save Course</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import { createClass } from '../../utils/api/classes';
import { getCareers } from '../../utils/api/career';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import classes from '../../utils/validations/classes';
import { success, fail } from '../../utils/toast';
import eventHub from '../../utils/eventhub';
import init from '../../config/tinymce';

export default {
	name: 'AddClass',
	mixins: [form],
	data() {
		return {
			init,
			itemData: {},
			loadingCareers: true,
			careersLoaded: false,
			loading: false,
			careers: [],
			errors: {},
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	created() {
		this.getCareers();
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	components: {
		Loader,
		quillEditor
	},
	methods: {
		getCareers() {
			this.loadingCareers = true;
			getCareers()
				.then((response) => {
					if (response.status === 200) {
						this.careers = response.data.data;
						this.careersLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loadingCareers = false;
					return error;
				});
			return true;
		},
		createClass() {
			this.validate(this.itemData, classes.create);

			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.itemData.author_id = this.$session.get('id');
			this.loading = true;
			createClass(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.loading = false;
						this.itemData = {};
						eventHub.$emit('class-created', response.data);
						success({
							title: 'Success! A new online class has been created'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		loadImage() {
			const img = this.$refs.avatar.files[0];
			const imageSize = 3 * (2 ** 20);
			if (img && img.type.match(/image\/*/)) {
				if (img.size > imageSize) {
					errors.file = 'File size greater than 3MB';
					return false;
				}
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.itemData.file = e.target.result;
			};
			reader.readAsDataURL(img);
			return true;
		}
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>

<style lang="scss" scoped>
input::placeholder {
	font-weight: normal;
}
.modal__form .form-control {
	height: 50px!important;
}
.modal__label {
	font-size: 14px!important;
}
.quill-editor {
	height: 20rem;
}
.modal__footer {
	margin-top: 7em;
}
.course-image-upload {
	margin-top: 8em;
}
</style>
