<template>
	<div>
		<template v-if="loadingClasses">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<div style="display: flex; margin-right: 20px;">
						<h4 class="main__title">Courses</h4>
						<p>{{total}} Total</p>
					</div>
					<div class="">
						<label
						class="form__label"
						for="search">
							<input
								id="search"
								class="form-control form__input form__search"
								type="search"
								v-model="query"
								@keydown.enter="search"
								name=""
								placeholder="Type in to search to search for courses">
							<span class="form__search__icon">
								<svg
								width="24"
								height="24"
								fill="none"
								xmlns="http://www.w3.org/2000/svg">
								<title>Search Icon</title>
								<path
									d="M15.5 14h-.79l-.28-.27A6.471 6.471 0 0 0 16 9.5 6.5 6.5 0 1 0 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"
									fill="#828282"/>
								</svg>
							</span>
						</label>
					</div>
				</div>

				<div class="d-flex">
					<button class="btn btn__primary" type="button" name="button" data-toggle="modal" data-target="#contactModal">
						<span class="btn__icon">
							<svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z" fill="#fff"/></svg>
						</span>Add Course</button>
				</div>
			</div>
			<section class="main-grid__row contacts">
				<div class="contacts__wrapper">
					<template v-if="classesLoaded && classes.length && !lcm">
						<ClassItem 
							v-for="classData in classes"
							:key="classData.id"
							:class-data="classData"/>
					</template>
					<template v-else-if="classesLoaded && classes.length && lcm">
						<ClassItem
							v-for="classData in classes"
							:key="classData.onlineClass.id"
							:class-data="classData.onlineClass"/>
					</template>
					<template v-else-if="classesLoaded">
						No Courses created yet
					</template>
					<template v-else>
						Error Loading courses
					</template>
				</div>
				<Pagination :options="{ limit, offset, total, baseUrl }"
					v-if="total > limit"/>
			</section>
			<AddClass />
		</template>
	</div>
</template>

<script>
import AddClass from './AddClass';
import ClassItem from './ClassItem';
import { getClasses, getLCMClasses } from '../../utils/api/classes';
import Loader from '../../components/Loader';
import Pagination from '../../components/Pagination';
import eventHub from '../../utils/eventhub';

export default {
	name: 'Index',
	components: {
		AddClass, ClassItem, Loader, Pagination
	},
	data() {
		return {
			loadingClasses: true,
			classesLoaded: false,
			classes: [],
			baseUrl: 'classes',
			limit: 10,
			offset: 0,
			total: 0,
			query: '',
			lcm: true
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getClasses();
		eventHub.$on('class-created', (payload) => {
			this.classes.unshift(payload);
		});
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getClasses();
		}
	},
	methods: {
		getClasses() {
			let model = getLCMClasses(this.$session.get('id'), this.limit, this.offset);
			if (this.$session.get('category') !== 'learning-content-manager') {
				model = getClasses(this.limit, this.offset);
				this.lcm = false;
			}
			this.loadingClasses = true;
			model
				.then((response) => {
					if (response.status === 200) {
						this.classes = response.data.data;
						this.total = response.data.total;
						this.classesLoaded = true;
						return true;
					}
					this.classesLoaded = false;
					return false;
				})
				.catch((error) => {
					this.loadingClasses = false;
					return error;
				})
				.finally(() => {
					this.loadingClasses = false;
				});
			return true;
		},
		search() {
			this.$router.push({
				name: 'search-courses',
				params: {
					query: this.query
				}
			});
			this.query = '';
		}
	}
};
</script>

<style lang="scss" scoped>
.main__title {
	font-size: 24px;
	font-weight: bold;
}
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
</style>
